import React from 'react';
import './style.scss'
import {dsnCN} from "../../hooks/helper";
import { useTranslation } from "react-i18next";

function InfoBox({className}) {
    const { t } = useTranslation();
    return (

        <div className={dsnCN('box-info-contact', className)}>
            <h3>{t("new-project-title")}</h3>
            <p className="mt-10">
            {t("new-project-1")}
            </p>
            <br></br>
            <h6>France</h6>
            <p>
                20 Boulevard de Lyon <br/>
                67000 Strasbourg <br/>
                France
            </p>
            <br></br>
            <h6>United Kingdom</h6>
            <p>
            L3 HTM Business Park. Abergele Road. <br/>
            Rhuddlan. Denbighshire. LL18 5UP, UK.
            </p>
            <br></br>
            <h6>India</h6>
            <p>
            H-119, Block H, LGF, Sector 63,<br/> 
            Noida, Uttar Pradesh 201301<br/>
            </p>
        </div>


    );
}

export default InfoBox;