import React from 'react';
import {dsnCN} from "../../hooks/helper";

function AddressFooter({className, ...restProps}) {
    return (
        <div className={dsnCN('col-address' , className)} {...restProps}>
            <h6>France</h6>
            <p>
                20 Boulevard de Lyon <br/>
                67000 Strasbourg <br/>
                France
            </p>
            <br></br>
            <h6>United Kingdom</h6>
            <p>
            L3 HTM Business Park. Abergele Road. Rhuddlan. Denbighshire. LL18 5UP, UK.
            </p>
            <br></br>
            <h6>India</h6>
            <p>
            H-119, Block H, LGF, Sector 63,<br/> 
            Noida, Uttar Pradesh 201301<br/>
            </p>
        </div>
    );
}

export default AddressFooter;